

table {
  color: #333;
  background: white;
  border: 1px solid grey;
  font-size: 17px;
 
}
table thead th,
table tfoot th {
  color: #fff;
  background: rgb(1, 1, 221);
}
table caption {
  padding:.5em;
}
table th,
table td {
  padding: .5em;
  border: 1px solid lightgrey;
}

